html, body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: 500;
}

*, :after, :before {
  box-sizing: border-box;
}

:root {
  --color-blue: #00acdc;
  --color-black: #000;
}

.header, .container {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  display: flex;
}

.header__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.header__item:not(:first-child) {
  margin-left: 20px;
}

.header__link {
  text-transform: uppercase;
  color: var(--color-black);
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  display: flex;
}

.header__link:hover {
  color: var(--color-blue);
}

.header__link.is-active {
  color: var(--color-blue);
  position: relative;
}

.header__link.is-active:after {
  content: "";
  background-color: var(--color-blue);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}
